.peopleThoughts {
	// .peopleThoughts__content
	&__content {
		padding: 0 24px 100px;
		max-width: 1680px;
		background: $bccolor;
		border-radius: 8px;
		margin: 0px auto;
		@media (max-width: $md2+px) {
			padding: 0 24px 70px;
		}
		@media (max-width: $md2+px) {
			padding: 0 24px 50px;
		}
		@media (max-width: $md3+px) {
			padding: 0 15px 35px;
		}
		@media (max-width: $md4+px) {
			padding: 0 15px 10px;
		}
	}

	// .peopleThoughts__top
	&__top {
		margin-bottom: 73px;
		text-align: center;
		@media (max-width: $md3+px) {
			margin-bottom: 50px;
		}
		@media (max-width: $md4+px) {
			margin-bottom: 35px;
		}
	}

	// .peopleThoughts__middle
	&__middle {
	}
}

.top-thoughts {
	// .top-thoughts__line
	&__line {
		margin-bottom: 20px;
		transform: translateY(-50%);
		@media (max-width: $md3+px) {
			margin-bottom: 10px;
		}
		@media (max-width: $md4+px) {
			margin-bottom: 5px;
		}
	}

	// .top-thoughts__title
	&__title {
		margin-bottom: 35px;
		@media (max-width: $md3+px) {
			margin-bottom: 20px;
		}
	}

	// .top-thoughts__subtitle
	&__subtitle {
		font-family: $font-family-base;
		font-style: normal;
		font-weight: bold;
		font-size: 2.4rem;
		line-height: 150%;
		text-align: center;
		letter-spacing: 0.15em;
		color: #ffffff;
		@media (max-width: $md2+px) {
			font-size: 2rem;
		}
		@media (max-width: $md3+px) {
			font-size: 1.6rem;
		}
		@media (max-width: $md4+px) {
			font-size: 1.2rem;
		}
	}
}

.middle-thoughts {
	// .middle-thoughts__grid
	&__grid {
		margin: 0px auto;
		max-width: 1044px;
		display: grid;
		grid-template: 1fr 1fr / 1fr 1fr;
		gap: 24px;
		@media (max-width: $md3+px) {
			gap: 15px;
		}
		@media (max-width: 600px) {
			grid-template: 1fr 1fr 1fr 1fr / 1fr;
		}
	}

	// .middle-thoughts__cell
	&__cell {
		background: #02223b;
		box-shadow: 0px 20px 30px rgba(0, 201, 224, 0.03);
		border-radius: 5px;
		@media (min-width: $md2+px) {
			transform: translate3d(30px, 30px, 30px);
			opacity: 0;
			transition: all 0.5s ease 0.2s;
			&._active {
				transform: translate3d(0px, 0px, 0px);
				opacity: 1;
			}
		}
	}
}
.cell-people {
	// .cell-people__row
	&__row {
		display: flex;
		margin: 75px 75px 75px 40px;
		@media (max-width: $md2+px) {
			margin: 35px 35px 35px 20px;
		}
		@media (max-width: $md3+px) {
			margin: 20px 15px 20px 15px;
			display: block;
		}
	}

	// .cell-people__apokrif-wrap
	&__apokrif-wrap {
		flex: 0 0 40px;
		position: relative;
		width: 32px;
		height: 25px;
		margin-right: 20px;
		@media (max-width: $md3+px) {
			margin-bottom: 15px;
			width: 25px;
			height: 20px;
			flex: 0 0 32px;
		}
	}

	// .cell-people__apokrif-icon
	&__apokrif-icon {
		color: $primary;
	}

	// .cell-people__content
	&__content {
		flex: 1 1 auto;
	}

	// .cell-people__text
	&__text {
		text-transform: none;
		margin-bottom: 25px;
		line-height: 1.9;
		letter-spacing: 0;
		span {
			display: block;
			margin-top: 5px;
			font-weight: 300;
		}
		@media (max-width: $md3+px) {
			margin-bottom: 15px;
		}
	}

	// .cell-people__row-bottom
	&__row-bottom {
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}

	// .cell-people__image-wrap
	&__image-wrap {
		width: 80px;
		height: 80px;
		margin-right: 15px;
	}

	// .cell-people__image-text
	&__image-text {
	}
}
