/* Titles
 ========================================================================= */

/**
 * Responsive titles
 */
h1, .h1
    // @include responsive-tlt(50px, 60px)
    line-height: 128%
    font-family: $font-family-alternate
    font-weight: 700
    letter-spacing: -0.02em
    font-style: normal
    font-size: 12rem

    @media (max-width: $md1+px)
            font-size: 9rem
    @media (max-width: 1400px)
            font-size: 7rem
    @media (max-width: $md2+px)
            font-size: 5rem
    @media (max-width: $md3+px)
            font-size: 4rem

h2, .h2
    // @include responsive-tlt(34px, 41px)
    line-height: 1.1
    font-family: $font-family-alternate
    font-weight: 700
    font-style: normal
    font-size: 6.5rem
    letter-spacing: -0.02em

    @media (max-width: $md1+px)
            font-size: 4.1rem
    @media (max-width: $md2+px)
            font-size: 3.4rem
    @media (max-width: $md3+px)
            font-size: 2.6rem 

h3, .h3
    // @include responsive-tlt(19px, 23px)
    font-family: $font-family-base
    font-style: normal
    font-weight: 400
    font-size: 1.7rem 
    line-height: 150%
    
    @media (max-width: $md1+px)
            font-size: 1.5rem
    @media (max-width: $md2+px)
            font-size: 1.3rem
    @media (max-width: $md3+px)
            font-size: 1.1rem
    @media (max-width: $md4+px)
            font-size: 1rem

h4, .h4
    // @include responsive-tlt(1.6rem, 2rem)
    font-family: $font-family-base
    font-style: normal
    font-weight: 700
    line-height: 24/18
    font-size: 1.8rem
    letter-spacing: 0.1em
    text-transform: uppercase

    @media (max-width: $md1+px)
            font-size: 1.5rem
    @media (max-width: $md2+px)
            font-size: 1.5rem
    @media (max-width: $md3+px)
            font-size: 1.3rem

h5, .h5
    // @include responsive-tlt(1.6rem, 1.8rem)
    font-family: $font-family-base
    font-style: normal
    font-weight: 700
    line-height: 1.7
    font-size: 1.2rem
    letter-spacing: 0.15em
    text-transform: uppercase

    @media (max-width: $md1+px)
            font-size: 1.2rem
    @media (max-width: $md3+px)
            font-size: 1rem

h6, .h6
    // @include responsive-tlt(15px, 20px)
    font-family: $font-family-base
    font-style: normal
    font-weight: 400
    line-height: 1.5
    font-size: 1.7rem
    letter-spacing: -0.02em

    @media (max-width: $md1+px)
            font-size: 1.5rem
    @media (max-width: $md2+px)
            font-size: 1.3rem
    @media (max-width: $md3+px)
            font-size: 1.1rem
    @media (max-width: $md4+px)
            font-size: 1.0rem

.logo
    font-family: $font-family-accent
    font-style: normal
    font-weight: 700
    line-height: 1
    color: $white
    font-size: 3.6rem
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25)
    position: relative
    span
        color: $primary