.trip {
	padding-bottom: 200px;	
	
	@media (max-width: 1200px) {
		background-position: left 100%;
	}
	@media (max-width: $md1+px) {
		padding-bottom: 100px;
	}
	@media (max-width: $md2+px) {
		background-position: left 40%;
		padding-bottom: 50px;
	}
	@media (max-width: $md3+px) {
		padding-bottom: 35px;
	}
	@media (max-width: 600px) {
		background-position: left 50%;
		background-size: 70% auto;
	}

	// .trip__wrapper
	&__wrapper {
	}

	// .trip__wrapper-top
	&__wrapper-top {		
	}

	// __wrapper-bottom
	&__wrapper-bottom {		
	}

	// .trip__top
	&__top {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: 8%;
		@media (max-width: 700px) {
			flex-direction: column;
			margin-bottom: 30px;
			position: relative;
		}
	}

	// .trip__title
	&__title {
		align-self: flex-start;
		@media (max-width: 700px) {
			align-self: center;
		}
	}

	// .trip__crest
	&__crest {
		width: 24px;
		height: 24px;
		margin: 0 33px;
		position: relative;
		@media (max-width: 700px) {
			margin: 10px;
		}
	}

	// .trip__crest-icon
	&__crest-icon {
		stroke: $primary;
		stroke-width: 2;
	}

	// .trip__text
	&__text {
		@media (max-width: 700px) {
			text-align: center;
		}
	}

	// .trip__pagination
	&__pagination {
		margin-bottom: 140px;
		display: flex;
		align-items: center;
		&_wrapper {
			flex: 1 1 auto;
		}
		@media (max-width: 700px) {
			margin-bottom: 100px;
		}
	}

	// __pagination_wrapper
	&__pagination_wrapper {
		position: relative;
	}

	// .trip__row, .trip__pagination-shadow
	&__row,
	&__pagination-shadow {
		display: flex;
		align-items: center;
		justify-content: space-around;
	}

	// .trip__slide-name
	&__slide-name {
		font-family: $font-family-base;
		font-style: normal;
		font-weight: 700;
		font-size: 1.6rem;
		line-height: 1.1;
		opacity: 0.5;
		text-align: center;
		letter-spacing: -0.02em;
		position: relative;
		padding: 10px 0 25px;
		transition: all 0.3s ease 0s;
		cursor: pointer;

		@media (max-width: $md3+px) {
			font-size: 1.4rem;
			border-bottom: 1.5px solid $primary;
		}
	}

	// .trip__slider-area
	&__slider-area {
		display: flex;
		@media (max-width: 1200px) {
			flex-direction: column;
		}
	}

	// .trip__slider-wrap
	&__slider-wrap {
		flex: 1 1 auto;
		
		// Спецвставка
		min-width: 0;

		@media (min-width: $md2+px) {
			transform: translateY(100px);
			opacity: 0;
			transition: all 0.4s ease 0s;
			&._active {
				transform: translateY(0);
				opacity: 1;
			}
		}
	}

	// .trip__slider
	&__slider {
	}

	// .trip__social
	&__social {
		flex: 1 0 143px;
		align-self: flex-end;
		@media (max-width: $md2+px) {
			flex: 0 0 0px;
		}
	}
}

.icon {
}
.icon-crest {
}
.slider {	
	// .slider__slide
	&__slide {
	}

	// .slider__row
	&__row {
		display: flex;
		justify-content: flex-start;
		@media (max-width: $md2+px) {
			display: block;
		}
	}

	// .slider__image-column
	&__image-column {
		flex: 1 0 36%;
		margin-right: 10%;
		margin-left: 75px;
		min-height: 200px;
		position: relative;

		@media (max-width: $md2+px) {
			margin-right: 0;
			margin-left: 100px;
			padding-bottom: 50%;
			height: 0;
		}
		@media (max-width: 600px) {
			margin-left: 0;
			padding-bottom: 60%;
		}
	}

	// .slider__image-wrap
	&__image-wrap {
		position: relative;
		z-index: 2;
		height: 100%;
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		border-radius: 1rem;
		overflow: hidden;
		padding-bottom: 54%;
		@media (max-width: $md2+px) {
			padding-bottom: 65%;
		}
	}
	// .slider__image
	&__image {
	}

	// .slider__control-btn
	&__control-btn {
		position: absolute;
		width: 130px;
		height: 65px;
		bottom: -1px;
		right: -3px;
		display: flex;
		border-radius: 10px 0 0 0;
		overflow: hidden;
		@media (max-width: $md3+px) {
			width: 100px;
			height: 50px;
		}
		@media (max-width: $md4+px) {
			width: 80px;
			height: 40px;
		}

		&_column {
			flex: 1 1 50%;
			background: #011627;
			box-shadow: -10px 14px 24px rgba(1, 22, 39, 0.08);
			display: flex;
			align-items: center;
			justify-content: center;
			border-left: 3px solid transparent;
			margin-right: 2px;
			cursor: pointer;
			@media (min-width: $md2+px) {
				&:hover {
					background: darken($primary, 18%);
				}
			}
		}
	}
	// .slider__control
	&__control {
		width: 24px;
		height: 24px;
		position: relative;
		@media (max-width: $md3+px) {
			width: 18px;
			height: 18px;
		}
	}

	// .slider__control-left
	&__control-left {
	}

	// .slider__control-left-icon
	&__control-left-icon {
		stroke: $white;
		transform: rotate(-90deg);
	}

	// .slider__control-right
	&__control-right {
	}

	// .slider__control-right-icon
	&__control-right-icon {
		stroke: $white;
		transform: rotate(90deg);
	}

	// .slider__image-text-block
	&__image-text-block {
		position: relative;
		z-index: 3;
		width: 220px;
		top: -60%;
		left: -20%;

		&::before {
			content: "";
			position: absolute;
			height: 20%;
			width: 2px;
			background-color: $primary;
			left: -5px;
			top: -20px;
		}
		&::after {
			content: "";
			position: absolute;
			height: 2px;
			width: 80%;
			background-color: $primary;
			left: -5px;
			top: -20px;
		}
		@media (max-width: $md2+px) {
			transform: translateY(-170%);
			left: -15%;
		}
		@media (max-width: 600px) {
			display: none;
		}
	}

	// .slider__number
	&__number {
		font-family: $font-family-base;
		font-style: normal;
		font-weight: 800;
		font-size: 2.4rem;
		line-height: 1.5;
		letter-spacing: 0.15em;
		color: $primary;
	}

	// .slider__image-text
	&__image-text {
		font-family: $font-family-base;
		font-style: normal;
		font-weight: 700;
		font-size: 2.4rem;
		line-height: 1.5;
		letter-spacing: 0.15em;
		@media (max-width: $md2+px) {
			font-size: 1.8rem;
		}
		@media (max-width: $md4+px) {
			font-size: 1.6rem;
		}
	}

	// .slider__decoration-dots
	&__decoration-dots {
		position: relative;
		z-index: 1;
		top: -49%;
		left: -13%;
		width: 230px;
		height: 230px;

		&_icon {
			color: $primary;
		}

		@media (max-width: $md2+px) {
			width: 150px;
			height: 150px;
			top: -0%;
			left: 60%;
			transform: translateY(-110%);
		}
		@media (max-width: 600px) {
			transform: translateY(-80%);
		}
	}

	// .slider__text-column
	&__text-column {
		flex: 1 1 60%;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
		&_top {
			flex: 0 1 70%;
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			justify-content: center;
			margin-top: 65px;
		}
		&_bottom {
			align-self: flex-end;
			flex: 0 1 30%;
		}

		@media (max-width: $md2+px) {
			margin-left: 50px;
		}
		@media (max-width: 600px) {
			margin-top: 0;
			margin-left: 10px;
		}
	}

	// .slider__title.title-text
	&__title {
	}

	// .slider__text
	&__text {
		margin-bottom: 60px;
		max-width: 480px;
		p {
			margin-top: 1.5rem;
		}
		@media (max-width: 600px) {
			margin-bottom: 30px;
		}
	}

	// .slider__btn
	&__btn {
		margin-bottom: 45px;
		@media (max-width: $md2+px) {
			margin-bottom: 20px;
		}
	}

	// .slider__social
	&__social {
		align-self: flex-end;
		position: relative;
	}

	// .slider__social-title
	&__social-title {
		margin-bottom: -14px;
		font-family: $font-family-base;
		font-style: normal;
		font-weight: bold;
		font-size: 1.5rem;
		line-height: 1.5;
		letter-spacing: 0.15em;
		@media (max-width: $md3+px) {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 43%;
			left: -80%;
		}
		@media (max-width: $md4+px) {
			font-size: 1.3rem;
			left: -70%;
		}
	}

	// .slider__social-icons
	&__social-icons {
		display: flex;
		margin-bottom: 20px;
		a {
			margin-right: 23px;
		}
		@media (max-width: $md3+px) {
			margin-top: -10px;
		}
	}

	// .slider__social-line
	&__social-line {
		position: relative;
		height: 2px;
		width: 133px;
		background-color: $primary;

		&::after {
			content: "";
			position: absolute;
			height: 20px;
			width: 2px;
			background-color: $primary;
			right: 0px;
			top: -20px;
		}
		@media (max-width: $md3+px) {
			width: 133px;
			height: 1px;
		}
		@media (max-width: $md4+px) {
			margin-top: -12px;
		}
	}

	// .slider__tweet-icon
	&__tweet-icon {
	}

	// .slider__inst-icon
	&__inst-icon {
	}

	// .slider__facebook-icon
	&__facebook-icon {
	}

	// .slider__tweet
	&__tweet {
	}

	// .slider__inst
	&__inst {
	}

	// .slider__facebook
	&__facebook {
	}
}
.icon-arrow-up {
	fill: $white;
}
.icon-twitter,
.icon-instagram,
.icon-facebook {
	@media (max-width: $md4+px) {
		width: 18px;
		height: 18px;
	}
}
