@use "sass:math";

.icon {
    display: inline-block;
    width: 1em;
    height: 1em;
    fill: currentColor;
}

.icon-apokrif {
    width: math.div(32, 25) * 1em;
}
.icon-arrow-up {
    width: math.div(32, 32) * 1em;
}
.icon-crest {
    width: math.div(24, 24) * 1em;
}
.icon-dots {
    width: math.div(230, 230) * 1em;
}
.icon-dotsVertical {
    width: math.div(70, 470) * 1em;
}
.icon-facebook {
    width: math.div(24, 24) * 1em;
}
.icon-instagram {
    width: math.div(24, 24) * 1em;
}
.icon-search {
    width: math.div(50, 50) * 1em;
}
.icon-twitter {
    width: math.div(24, 24) * 1em;
}
