.aboutUs {
	margin-top: 45px;
	margin-bottom: 200px;

	@media (max-width: $md1+px) {
		margin-top: 0px;
		margin-bottom: 100px;
	}
	@media (max-width: $md3+px) {
		margin-bottom: 45px;
	}
	@media (max-width: $md4+px) {
		margin-bottom: 35px;
	}

	// .aboutUs__content
	&__content {
	}

	// .aboutUs__row
	&__row {
		@media (min-width: $md3+px) {
			display: flex;
			align-items: flex-start;
			justify-content: center;
		}
	}

	// .aboutUs__column
	&__column {
		// .aboutUs__column_text
		&_text {
			flex: 0 1 45%;
			margin-left: 10px;
			@media (min-width: $md2+px) {
				flex: 0 1 30%;
				margin-right: 80px;
				margin-top: 50px;
			}
			@media (min-width: 600px) {
				margin-left: 45px;
				margin-right: 40px;
			}
		}

		// .aboutUs__column_photos
		&_photos {
			flex: 0 1 70%;
		}
	}
}

.text-about {
	@media (max-width: $md3+px) {
	}
	// .text-about__title.title-text
	&__title {
	}

	// .text-about__text
	&__text {
		font-weight: 700;
		margin-bottom: 40px;
		p {
			font-weight: 400;
		}
		@media (max-width: $md3+px) {
			margin-bottom: 25px;
		}
	}

	// .text-about__btn
	&__btn {
		display: inline-block;
		@media (max-width: $md2+px) {
			margin-bottom: 30px;
		}
	}
}
.photo-about {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	width: 100%;
	@media (min-width: $md2+px) {
		transform: translateX(100px);
		opacity: 0;
		transition: all 0.4s ease 0s;
		&._active {
			transform: translateX(0);
			opacity: 1;
		}
	}

	// .photo-about__images-row
	&__images-row {
		flex-grow: 1;
		display: flex;
		flex-wrap: wrap;
		margin-right: 10px;
	}

	// .photo-about__image-wrap
	&__image-wrap {
		flex: 0 1 50%;
		padding-bottom: 48.2%;
		position: relative;
		
		&::after {
			content: "";
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			background-color: rgba(0, 0, 0, 0.5);
			transition: all 0.6s linear 0s;			
		}
		&:hover {
			animation-play-state: pause;
			&::after {
				background-color: transparent;
			}
			.photo-about__spoiler {
				opacity: 1;
			}
		}
		&._active {
			&::after {
				background-color: transparent;
			}
			.photo-about__spoiler {
				opacity: 1;
			}
		}
	}

	// .photo-about__image
	&__image {
	}

	// .photo-about__spoiler
	&__spoiler {
		transition: all 0.5s ease 0s;
		position: absolute;
		left: -10%;
		bottom: 10%;
		border-radius: 10px;
		padding: 26px 19px;
		background-color: #fff;
		font-family: $font-family-base;
		font-style: normal;
		font-weight: bold;
		font-size: 1.5rem;
		line-height: 150%;
		letter-spacing: 0.05em;
		color: $black;
		opacity: 0;
		z-index: 3;
		span {
			color: $primary;
		}
		@media (max-width: $md2+px) {
			padding: 10px 12px;
			font-size: 1.4rem;
		}
		@media (max-width: $md4+px) {
			padding: 8px 10px;
			font-size: 1.1rem;
			line-height: 120%;
		}		
	}

	// .photo-about__dots
	&__dots {
		position: relative;
	}

	// .photo-about__dots-icon
	&__dots-icon {
		width: 70px;
		height: 470px;
		transform: translateY(-50%);
		color: $primary;
		@media (max-width: $md2+px) {
			width: 40px;
			height: 300px;
		}
	}
}
