/* Include fonts
   ========================================================================= */
/**
 * Playfair, Larsseit, NoeDisplay
 */
@import url(https://fonts.googleapis.com/css?family=Playfair+Display:700&display=swap)

@include fontFace('Larsseit', 'Larsseit', 400, 'regular', 'woff2')
@include fontFace('Larsseit', 'Larsseit-Light', 300, 'light', 'woff2')
@include fontFace('Larsseit', 'Larsseit-Bold', 700, 'bold', 'woff2')
@include fontFace('Larsseit', 'Larsseit-ExtraBold', 800, 'extraBold', 'woff2')
@include fontFace('NoeDisplay', 'NoeDisplay-Bold', 700, 'bold', 'woff2')