// Font
$font-family-base: "Larsseit";
$font-family-alternate: "NoeDisplay";
$font-family-accent: "PlayfairDisplay";
$enable-smooth-scroll: false;
$fontSize: 62.5%;

// Границы адаптива ======================
$minWidth: 320px; // Указываем минимальный размер экрана
$maxWidth: 1920; // Указываем размер полотна
$maxWidthContainer: 1450; // Указываем размер ограничивающего контейнера
$smallMaxWidthContainer: 1214; // Указываем размер малого ограничивающего контейнера

$md1: $maxWidthContainer + 12;
$md2: 991.98;
$md3: 767.98;
$md4: 479.98;

//<ОСНОВНАЯ СЕТКА>================================================================
._container {
	overflow: hidden;
	max-width: $maxWidthContainer + px;
	margin: 0 auto;
	padding: 0 40px;
	@media (max-width: $md1+px) {
		max-width: 1390px;
	}
	@media (max-width: $md2+px) {
		max-width: 750px;
	}
	@media (max-width: $md3+px) {
		max-width: none;
		padding: 0 30px;
	}
	@media (max-width: $md4+px) {
		max-width: none;
		padding: 0 20px;
	}
}
._smallContainer {
	max-width: $smallMaxWidthContainer + px;
	margin: 0 auto;
	
	@media (max-width: $md1+px) {
		max-width: 1390px;
		padding: 0 40px;
	}
	@media (max-width: $md2+px) {
		max-width: 750px;
		padding: 0 25px;
	}
	@media (max-width: $md3+px) {
		max-width: none;
		padding: 0 30px;
	}
	@media (max-width: $md4+px) {
		padding: 0 20px;
	}
}

//</ОСНОВНАЯ СЕТКА>=================================================================

