// <ОБОЛОЧКА>=======================
html {
	font-size: 10px;
	color: $white;
}

.wrapper {
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;

	&._loaded {
	}
}
.wrapper-pickAndAboutAndThoughts {
	position: relative;
}
.bg-block-wrap {
	position: absolute;
	width: 100%;
	height: 100%;
	top: -15%;
	left: 25%;
	transform: rotate(135deg);
	background: url(../images/pickTrip/bgLight.webp) -10% 40% / 60% auto no-repeat;
}
.bg-block-wrap2 {
	position: absolute;
	width: 100%;
	height: 100%;
	top: -25%;
	right: 25%;
	background: url(../images/pickTrip/bgLight.webp) 40% 40% / 60% auto no-repeat;
}
.bg-block-wrap3 {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 35%;
	left: 37%;
	transform: rotate(135deg);
	background: url(../images/pickTrip/bgLight.webp) -10% 40% / 60% auto no-repeat;
}
//</ОБОЛОЧКА>=====================================================================
//<ОСНОВНОЙ БЛОК>====================================================================================================
body {
	font-size: 1.4rem;
	font-family: $font-family-base;
	background-color: $bccolor;
	&._lock {
		overflow: hidden;
	}
}

// new ================================================
.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
}

//</ОСНОВНОЙ БЛОК>====================================================================================================

//<ГОТОВЫЕ КЛАССЫ>====================================================================================================
.d-none {
	display: none !important;
}
// Высота 100% высоты экрана
.h100 {
	height: 100vh;
}
.h100s {
	@media (min-width: $md4+px) {
		height: 100vh;
	}
}
.title-text {
	margin-bottom: 30px;
	@media (max-width: $md3+px) {
		margin-bottom: 20px;
	}
}

.mt30 {
	margin-top: 30px;
}

._visible {
	overflow: visible;
}

.btn {
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 10px 25px;
	border-radius: $btn-border-radius;
	box-shadow: 5px 15px 40px rgba(0, 201, 224, 0.2);
	background: $primary-light;
	color: #fff;
	font-family: $font-family-base;
	font-style: normal;
	font-weight: 700;
	font-size: 1.4rem;
	line-height: 1.4;
	transition: all 0.3s ease 0s;
	letter-spacing: 0.15em;
	text-transform: uppercase;
	position: relative;
	cursor: pointer;
	&::after {
		content: "";
		position: absolute;
		width: 30px;
		height: 2px;
		background-color: $white;
		top: 50%;
		right: -15px;
		transition: all 0.5s ease 0s;
	}

	@media (min-width: $md2+px) {
		padding: 19px 47px;
		&:hover {
			box-shadow: 5px 15px 40px rgba(0, 201, 224, 0.4);
			&::after {
				right: -40px;
			}
		}
	}
}
//=======================================================================================================================
.icon {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}
// Линия декоративная у кнопок, бирюзового цвета
.line {
	margin: 0 auto;
	background: linear-gradient(180deg, #09f4f4 0%, #00c9e0 100%);
	border-radius: 13px;
	width: 2px;
	height: 80px;
	@media (max-width: $md3+px) {
		height: 40px;
	}
	@media (max-width: $md4+px) {
		height: 30px;
	}
}

//====================================================================================================
._ibg {
	position: relative;
	img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
	}
}
body.ie {
	._ibg {
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
	}
	._ibg img {
		width: 0;
		height: 0;
		opacity: 0;
		visibility: hidden;
	}
}
//====================================================================================================

//</ГОТОВЫЕ КЛАССЫ>===================================================================================
