.header {
	background: url(../images/header/bgImage.webp) center / cover no-repeat;
	position: relative;
	&::before {
		content: "";
		position: absolute;
		width: 100%;
		height: 32%;
		bottom: 0;
		right: 0;
		background: linear-gradient(180deg, rgba(1, 22, 39, 0) 0%, #011627 76.64%, #011627 100%);
	}

	// .header__content
	&__content {
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		height: 100%;
	}

	// .header__menu
	&__menu {
	}

	// .header__text-block
	&__text-block {
		margin: 0 auto;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		max-width: 65rem;
		max-height: 32rem;
		text-align: center;
	}

	// .header__title
	&__title {
		margin-bottom: 3.5rem;
		@media (max-width: $md3+px) {
			margin-bottom: 2rem;
		}
	}

	// .header__subtitle
	&__subtitle {
		margin-bottom: 2rem;
		@media (min-width: $md2+px) {
			margin-bottom: 6rem;
		}
	}

	// .header__text
	&__text {
	}

	// .header__bottom
	&__bottom {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	// .header__scroll
	&__scroll {
		transform: rotate(-90deg) translate(6.5rem, -2.5rem);
		@media (max-width: $md3+px) {
			transform: rotate(-90deg) translate(2.5rem, -2.5rem);
		}
		// .header__scroll_text
		&_text {
			font-family: $font-family-base;
			font-style: normal;
			font-weight: bold;
			font-size: 1.5rem;
			line-height: 190%;
			letter-spacing: 0.05em;
			color: $white;
			text-transform: uppercase;
			position: relative;

			&::before {
				content: "";
				position: absolute;
				left: -150%;
				top: 50%;
				width: 80px;
				height: 2px;
				background: linear-gradient(180deg, #09f4f4 0%, #00c9e0 100%);
				border-radius: 1.3rem;
				animation-name: scrollDown;
				animation-duration: 3s;
				animation-timing-function: ease-in-out;
				animation-iteration-count: infinite;
				animation-direction: normal;
			}
			@media (max-width: $md3+px) {
				font-size: 1.2rem;
				&::before {
					width: 60px;
				}
			}
		}

		// .header__scroll_line
		&_line {
		}
	}

	// .header__social
	&__social {
		// .header__social-link
		&-link {
			margin-top: 30px;
			width: 24px;
			height: 24px;
			position: relative;
			color: transparent;
			stroke: $primary;
			stroke-width: 2;
			display: block;
			transition: all 0.3s ease 0s;

			@media (min-width: $md2+px) {
				&:hover {
					transform: scale(1.4);
				}
			}
		}
		// .header__social_twitter
		&_twitter {
		}

		// .header__social_twitter-icon
		&_twitter-icon {
		}

		// .header__social_instagram
		&_instagram {
		}

		// .header__social_twitter-instagram
		&_twitter-instagram {
		}

		// .header__social_facebook
		&_facebook {
		}

		// .header__social_twitter-facebook
		&_twitter-facebook {
		}
	}
}

.menu {
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;

	// .menu__column
	&__column {
		display: flex;
	}

	// .menu__logo
	&__logo {
	}

	// .menu__links
	&__links {
		transition: all 0.3s ease 0s;
		color: $primary-light;
		position: absolute;
	}

	// .menu__link
	&__link {
		&._active {
			transform: translate(0rem, 6.5rem);
			opacity: 1;
			display: block;
		}
		opacity: 0;
		display: none;
		border-radius: $btn-border-radius;
		margin-bottom: 1rem;
		transform: translate(0rem, 0rem);
		color: $primary-light;
		display: inline-block;
		background-color: #fff;
		width: 100%;
		height: 100%;
		transition: all 0.5s ease 0s;
		&::after {
			content: "";
			opacity: 0;
			right: -25%;
			top: 50%;
			width: 0rem;
		}

		@media (min-width: 320px) {
			font-size: 1.2rem;
			padding: 1.3rem 1.1rem;
			text-align: center;
		}
		@media (min-width: $md2+px) {
			font-size: 1.4rem;
			padding: 1.5rem 2rem;
			&:hover {
				transform: scale(1) translate(-1rem, 6.5rem);

				&::after {
					opacity: 1;
					position: absolute;
					width: 3rem;
					height: 0.3rem;
					background-color: $primary-light;
					right: -10%;
					transition: all 0.4s ease 0s;
				}
			}
		}
	}

	// .menu__form
	&__form {
		display: flex;
		align-items: center;
		justify-content: flex-end;				
	}

	// .menu____input-search
	&__input-search {
		&._active {
			width: 100%;
			opacity: 1;
			@media (max-width: 600px){
				position: absolute;
				width: 100%;
				left: 0;
				top: -40px;
			}
		}
		opacity: 0;
		transition: all 0.8s ease 0s;
		margin-right: 30px;
		height: 2.5rem;
		border-radius: 1rem;
		width: 0%;
	}

	// .menu__search
	&__search {
		height: 28px;
		min-width: 28px;
		position: relative;
		margin-right: 32px;

		// .menu__search_icon
		&_icon {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			color: $white;
			transition: all 0.3s ease 0s;
			@media (min-width: $md2+px) {
				&:hover {
					transform: scale(1.4);
				}
			}
		}
	}

	// .menu__btn
	&__btn {
		position: relative;
		z-index: 2;
	}

	// .menu__btn-column
	&__btn-column {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}

