.footer {
	// .footer__content
	&__content {
		height: 100%;
		padding-top: 50px;
		padding-bottom: 150px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		@media (max-width: $md2+px) {
			padding-top: 45px;
			padding-bottom: 45px;
		}
		@media (max-width: $md3+px) {
			justify-content: center;
			padding-top: 25px;
			padding-bottom: 25px;
		}
	}

	// .footer__top
	&__top {
		max-width: 425px;
		margin: 0 auto;
		margin-bottom: 135px;
		@media (max-width: $md1+px) {
			margin-bottom: 70px;
		}
		@media (max-width: $md2+px) {
			margin-bottom: 50px;
		}
		@media (max-width: $md3+px) {
			margin-bottom: 35px;
		}
		@media (max-width: $md4+px) {
			margin-bottom: 25px;
		}
	}

	// .footer__tittle
	&__tittle {
		margin-bottom: 30px;
		text-align: center;
		@media (max-width: $md4+px) {
			margin-bottom: 20px;
		}
	}

	// .footer__subtitle
	&__subtitle {
		max-width: 360px;
		text-align: center;
		margin-bottom: 25px;
		line-height: 2.1;
		letter-spacing: 0.1em;
		@media (max-width: $md4+px) {
			margin-bottom: 20px;
		}
	}

	// .footer__middle
	&__middle {
		transform: translateY(-50%);
		margin-bottom: 120px;
		@media (max-width: $md1+px) {
			margin-bottom: 70px;
		}
		@media (max-width: $md2+px) {
			margin-bottom: 50px;
		}
		@media (max-width: $md3+px) {
			margin-bottom: 35px;
		}
		@media (max-width: $md4+px) {
			margin-bottom: 25px;
		}
	}

	// .footer__bottom
	&__bottom {
	}

	// .footer__row
	&__row {
		@media (min-width: 600px) {
			display: flex;
			justify-content: space-between;
			align-items: flex-start;
			margin-bottom: 70px;
		}
		@media (max-width: $md3+px) {
			margin-bottom: 20px;
		}
		@media (max-width: $md4+px) {
			margin-bottom: 15px;
		}
	}

	// .footer__coryright
	&__coryright {
		text-align: center;
		margin-bottom: 25px;
		font-family: $font-family-base;
		font-style: normal;
		font-weight: normal;
		font-size: 1.5rem;
		line-height: 2.5rem;
		display: block;
		color: $white;
		@media (max-width: $md3+px) {
			margin-bottom: 10px;
		}
	}

	// .footer__logo
	&__logo {
		text-align: center;
		display: block;
	}
}

.link-footer {
	// .link-footer__column
	&__column {
		// .link-footer__column_left
		&_left {
			flex: 1 1 auto;
			display: flex;
			align-items: flex-start;
			max-width: 850px;
			flex-wrap: wrap;
		}

		// .link-footer__column_right
		&_right {
			flex: 0 0 15%;
			justify-self: flex-end;
			@media (max-width: $md2+px) {
				flex: 0 0 35%;
			}

			.link-footer__theme {
				margin: 0;
			}

			.link-footer__title {
				@media (max-width: $md2+px) {
					text-align: center;
				}
			}

			.link-footer__line {
				@media (max-width: $md2+px) {
					margin: 0 auto 18px;
				}
			}
		}
	}

	// .link-footer__theme
	&__theme {
		flex: 0 1 25%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		margin-right: 0%;

		@media (max-width: $md2+px) {
			flex: 0 1 40%;
			flex-wrap: wrap;
			margin-right: 7%;
			margin-bottom: 2.5rem;
		}

		@media (min-width: $md2+px) {
			transform: rotate(50deg);
			transform-origin:top center;
			opacity: 0;
			transition: all 0.45s ease 0.1s;
			&._active {
				transform: rotate(0deg);
				opacity: 1;
			}

			&:hover {
				.link-footer__line {
					width: 80px;
				}
			}
		}
	}

	// .link-footer__title
	&__title {
		text-transform: uppercase;
		margin-bottom: 1.8rem;
	}

	// .link-footer__line
	&__line {
		transition: all 0.3s ease 0s;
		width: 50px;
		height: 1px;
		background-color: $primary;
		margin-bottom: 18px;
		position: relative;
		&::after {
			content: "";
			width: 6px;
			height: 6px;
			top: -2.5px;
			right: -6px;
			position: absolute;
			border: 1px solid $primary;
			box-sizing: border-box;
			transform: rotate(45deg);
		}
	}

	// .link-footer__link
	&__link {
		color: $white;
		margin-bottom: 1.8rem;
		font-family: $font-family-base;
		font-style: normal;
		font-weight: normal;
		font-size: 1.5rem;
		line-height: 1.9/1.5;
		transition: all 0.2s ease 0s;
		@media (max-width: $md3+px) {
			margin-bottom: 15px;
		}
		@media (max-width: $md4+px) {
			font-size: 1.2rem;
		}

		@media (min-width: $md2+px) {
			&:hover {
				color: $primary;
			}
		}
	}

	// .link-footer__social-links
	&__social-links {
		display: flex;
		justify-content: space-between;
		@media (max-width: $md2+px) {
			margin: 0px auto;
			width: 100%;
			max-width: 270px;
			display: flex;
			justify-content: space-around;
		}
	}

	// .link-footer__social-link
	&__social-link {
		margin-right: 18px;
		margin-bottom: 18px;
		margin-top: 18px;
		width: 24px;
		height: 24px;
		position: relative;
		color: transparent;
		stroke: $primary;
		stroke-width: 2;
		display: block;
		transition: all 0.3s ease 0s;

		@media (min-width: $md2+px) {
			&:hover {
				transform: scale(1.5);
			}
		}
		@media (max-width: $md2+px) {
			margin: 15px auto 25px;
		}
		@media (max-width: $md3+px) {
			margin: 10px auto 10px;
		}

		// .link-footer__social-link_twitter-icon
		&_twitter-icon {
		}

		// .link-footer__social-link_twitter-instagram
		&_twitter-instagram {
		}

		// .link-footer__social-link_twitter-facebook
		&_twitter-facebook {
		}
	}
}

